<script>
	import {Line} from "vue-chartjs";
	export default {
		extends: Line,
		props: {
			hashtag: String
		},
		data() {
			return {
				datacollection: null,
				//Chart.js options that controls the appearance of the chart
				options: {
					scales: {
						yAxes: [{
							ticks: {
								beginAtZero: true
							},
							gridLines: {
								display: true
							}
						}],
						xAxes: [{
							gridLines: {
								display: false
							}
						}]
					},
					legend: {
						display: true
					},
					responsive: true,
					maintainAspectRatio: false
				}
			};
		},
		mounted() {
			
		},
		created() {
			this.fetchData();
		},
		watch: {
			datacollection(){
				
				this.renderChart(this.datacollection, this.options);
			}
		},
		methods: {
			fetchData: function(){
				let label = [];
				let dataPositive = [];
				let dataNegative = [];
				label.push('2020 August 10');
				dataPositive.push(70);
				dataNegative.push(15);
				label.push('2020 August 11');
				dataPositive.push(160);
				dataNegative.push(45);
				label.push('2020 August 12');
				dataPositive.push(52);
				dataNegative.push(49);
				label.push('2020 August 13');
				dataPositive.push(260);
				dataNegative.push(350);
				let datacollection = {
					labels: label,
					datasets: [{
							label: "Positive Sentiment",
							backgroundColor: "#27ae60",
							borderColor: "#27ae60",
							fill: false,
							data: dataPositive
						},
						{
							label: "Negative Sentiment",
							backgroundColor: "#f87979",
							borderColor: "#f87979",
							fill: false,
							data: dataNegative
						}
					]
				}
				this.datacollection = datacollection;
				console.log(this.datacollection);
			}
		}
	};
</script>